.btnUpdata {
  border: 1px solid rgb(0, 160, 27);
  /* background: #000; */
  padding: 10px 20px;
  border-radius: 6px;
  background: rgb(0, 202, 34);
  color: #fff;
  opacity: 0.8;
  transition: all 0.2s ease-in-out;
  position: fixed;
  right: 40px;
  bottom: 40px;
  cursor: pointer;
}

.coinLiss {
  max-width: 1280px;
  padding: 10vh 40px 6vh;
  margin: auto;
}

.coinLiss__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;

  align-items: center;
  flex-wrap: wrap;
}
.coinLiss__item {
  padding: 0;
  margin: 20px auto;
  border: 1px solid #eee;
  width: 402px;
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.coinLiss__item p {
  border: 1px solid #eee;
  width: 50%;
  padding: 3% 5%;
  text-align: center;
}
.coinLiss__name {
  font-size: 18px;
  font-weight: 500;
}
.coinLiss__have {
  font-size: 18px;
  font-weight: 500;
}
