.cRed {
  background: rgba(255, 0, 0, 0.5);
}
.cBlue {
  background: rgb(6, 0, 255, 50%);
}
.cGreen {
  background: rgba(0, 255, 64, 0.5);
}
.coinLiss__outcome {
  margin: auto;
}
.coinLiss__openChart {
  padding: 0 !important;
}
.coinLiss__openChart a {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
}
